export const createIMG = props => {
  console.log(props[0]);
  console.log(props[1]);
  const imageWrapper = document.createElement("div");
  const image = document.createElement("img");

  image.src = props[0];
  image.alt = props[1];

  imageWrapper.className = "aspect-box";
  imageWrapper.appendChild(image);

  return imageWrapper;
};
